body {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

.breed-select {
  border: none;
  border-bottom: 2px solid #000;
  outline: none;
  margin-bottom: 50px;
  padding: 5px;
  font-size: 16px;
}

.breed-select:hover {
  border-bottom: 2px solid #3890ab;
}

.cat-info {
  display: flex;
}

.cat-info img {
  margin-right: 50px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #3890ab;
  border-radius: 50%;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

